<template>
  <userLoading :active="isLoading">
    <div class="loading-bg">
      <img src="../../assets/loading-img.png" alt="loading">
    </div>
  </userLoading>
  <div class="position-relative d-flex">
      <div class="container d-flex flex-column" style="min-height: 100vh;">
        <!-- <nav class="navbar navbar-expand-lg navbar-light px-0">
          <a class="navbar-brand" href="./index.html">Navbar</a>
        </nav> -->
        <div class="row my-auto pb-7 bg">
          <div class="col-md-4 d-flex flex-column">
            <div class="my-auto">
              <h2 class=""><strong>訂單成功</strong></h2>
              <form class="" @submit.prevent="payOrder">
              <table>
                <thead>
                  <th>品名</th>
                  <th>數量</th>
                  <th>單價</th>
                </thead>
                <tbody>
                  <tr v-for="item in order.products" :key="item.id">
                    <td><img :src="item.product.imageUrl" width="100"></td>
                    <td>{{ item.product.title }}</td>
                    <td>{{ item.qty }}/{{ item.product.unit }}</td>
                    <td>{{ item.final_total }}</td>
                  </tr>
                </tbody>
                <tfoot>
                  <tr>
                    <td width="100" class="font-weight-bold">總計</td>
                    <td>${{ order.total }}</td>
                  </tr>
                </tfoot>
              </table>
              <table>
                <tbody>
                  <tr>
                    <th width="100">Email</th>
                    <td>{{ order.user.email }}</td>
                  </tr>
                  <tr>
                    <th>姓名</th>
                    <td>{{ order.user.name }}</td>
                  </tr>
                  <tr>
                    <th>收件人電話</th>
                    <td>{{ order.user.tel }}</td>
                  </tr>
                  <tr>
                    <th>收件人地址</th>
                    <td>{{ order.user.address }}</td>
                  </tr>
                  <tr>
                    <th>付款狀態</th>
                    <td>
                      <span v-if="!order.is_paid">尚未付款</span>
                      <span v-else class="text-success">付款完成</span>
                    </td>
                  </tr>
                </tbody>
              </table>
              <div class="mt-3" v-if="order.is_paid === false">
                <button class="btn btn-danger">確認付款去</button>
              </div>
              <div v-else class="btn btn-success mt-3">
                <a href="./#/index" class="text-white text-decoration-none">付款完成</a>
              </div>
              </form>
              <!-- <a href="./index.html" class="btn btn-dark mt-4 px-5">Back To Home</a> -->
            </div>
          </div>
        </div>
      </div>
      <div class="w-md-50 w-100 position-absolute opacity-1" style="z-index: -1; min-height: 100vh; right: 0; background-image: url(https://beardpapas.com.tw/wp-content/uploads/elementor/thumbs/Banner-pc-01-1-qy73vhau64prrqxjd6rqltvnjcy3cw78iofcd7cbb4.webp);
      background-position: center center;background-size: cover;">
      </div>
    </div>
</template>

<script>
export default {
  name: 'CheckoutSuccess',
  data() {
    return {
      order:{
        user: {},
      },
      orderId: '',
      isLoading: false,
    };
  },
  methods: {
    getOrder(){
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/order/${this.orderId}`;
      this.isLoading = true;
      this.$http.get(url).then((res) => {
        console.log(res);
        this.isLoading = false;
        if(res.data.success){
          console.log(res.data.order);
          this.order = res.data.order;
          console.log(this.order);
        }
      })
    },
    payOrder(){
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/pay/${this.orderId}`;
      this.$http.post(url).then((res) => {
        if(res.data.success){
          this.getOrder();
      }})
    }
  },
  created() {
    this.orderId = this.$route.params.orderId;
    console.log(this.orderId);
    this.getOrder();
  },
}
</script>

<style>
.bg{
  background-color: white ;
  padding: 50px;
  border-radius: 20px;
  box-shadow: 0 10px 20px rgba(0,0,0,0.1);
}
</style>

